}

.catagolue-header label:after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 22px;
  transition: 0.3s;
}

.catagolue-header input:checked + label {
  background: #55b0da;
}

.catagolue-header input:checked + label:after {
  left: calc(100% - 3px);
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.catagolue-header label:active:after {
  width: 45px;
}

