.edit-form-wrapper {
  position: relative;
  background: #ffffff;
  /* max-width: 460px; */
  width: 45%;
  height: fit-content;
  margin: 50px auto;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  border-radius: 5px;
  color: black;
  text-align: start;
}

.edit-form-wrapper div {
  position: relative;
  padding: 10px;
  width: 100%;
}

.edit-form-wrapper input {
  outline: 0;
  background: #ececec;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 10px;
  box-sizing: border-box;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid white;
}

.edit-form-wrapper input:focus {
  border: 1px solid #02a360;
}

.edit-form-wrapper button {
  text-transform: uppercase;
  outline: 0;
  background: #02a360;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 16px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  border-radius: 5px;
  cursor: pointer;
}

.edit-form-wrapper button:hover,
.edit-form-wrapper button:active,
.edit-form-wrapper button:focus {
  background: #02a360;
}

.edit-form-wrapper .fas {
  color: #02a360;
  position: absolute;
  top: 15px;
  left: 12px;
}

.edit-form-wrapper label {
  padding: 10px;
}

.edit-form-wrapper textarea {
  width: 95%;
  resize: none;
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  .edit-form-wrapper {
    width: 75%;
  }
}
